export default {
    data() {
        return {
            scenicId: 0,
            goodsList: []
        }
    },
    onLoad: function (option) {
        this.scenicId = option.scenic_id
    },
    onShow() {
        this.getAllGoods()
    },
    methods: {
        getAllGoods() {
            var _this = this
            uni.request({
                url: this.$BASE_HOST + 'goods/getAllGoodsOfScenic',
                method: 'get',
                data: {
                    'scenic_id': _this.scenicId
                },
                success: function (res) {
                    console.log(res.data.data)
                    _this.goodsList = res.data.data
                    _this.goodsList.forEach((item, index) => {
                        item.goods_cover = _this.$BASE_HOST + item.goods_cover
                    })
                }
            })
        }
    }
}
